import React, { Component } from "react";
// React-Bootstrap
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Jumbotron from 'react-bootstrap/Jumbotron';

import Header from "../Header";
import Worker from "../Worker";
import Table from 'react-bootstrap/Table';
import API from "../../utils/API";





class Dictionary extends Component {

  // Setting the component's initial state
  state = {
    result: [], 
    initial: []
  };

  // when component is mounted (once only)
  componentDidMount() {
     this.searchPeople();
  }

  // custom method which runs API
  searchPeople = query => {
     API.search(query)
        .then(res => {this.setState({ 
          result: res.data.results,
          initial: res.data.results
        })})
        .catch(err => console.log(err));
  };

  // https://developer.mozilla.org/en-US/docs/web/javascript/reference/global_objects/array/sort
  // we sort 2 elements
  compare = (property) => {
   
    return function (a,b) {
      // change property into array
      const properties = property.split(".");
      switch (properties.length) {
        case 1:
          a = a[properties[0]];
          b = b[properties[0]];
          break;
        case 2:
          a = a[properties[0]][properties[1]];
          b = b[properties[0]][properties[1]];
          break;       
        case 3:
          a = a[properties[0]][properties[1]][properties[2]];
          b = b[properties[0]][properties[1]][properties[2]];
          break;
        default:
          a = a[properties[0]];
          b = b[properties[0]];
          break;
      }

      let comparison; 
      if (a < b) {
        comparison = -1; // a goes first
      } else if (b < a){
        comparison = 1; // b goes first
      } else {
        comparison = 0; // no change
      }
      // we return this to sort function
      return comparison;
    }
  }

  // sort wrapper
  sortRecords = (column) => {
    console.log("Sorting..." + column)
    // here the sorting will be different for different columns
    switch(column) {
      case "age":
        this.setState({
          result: this.state.result.sort(this.compare("dob.age"))
        })
        break;

      case "first":
        this.setState({
          result: this.state.result.sort(this.compare("name.first"))
        })
        break;

      case "last":
        this.setState({
          result: this.state.result.sort(this.compare("name.last"))
        })
        break;

      case "mobile":
        this.setState({
          result: this.state.result.sort(this.compare("phone"))
        })
        break;

      case "country":
        this.setState({
          result: this.state.result.sort(this.compare("location.country"))
        })
        break;

      default:
        break;
    }
  }
 
  // sort handler
  handleSort = event => {
    event.preventDefault();
    this.sortRecords(event.target.name);
  }

  // filter handler
  handleInputChange = event => {   
    event.preventDefault();
    // getting the value which triggered the change
    const reg  = `^${event.target.value}.*`;
    console.log("Filtering..." + reg)
    // filter per first name
    const filtered =  this.state.initial.filter(function(record){
      return record.name.first.match(reg);
    })
    
    if (filtered.length !== 0) {
      this.setState({
        result: filtered
      })
    } else {
      this.setState({
        result: this.state.initial
      })
    }       
  }

  render() {
    return (
        <Container fluid>   
          <Row>
            <Col className="text-center">               
                <h1>Employee directory</h1>
                  <Header handleInputChange={this.handleInputChange}/>
                    <Table striped bordered hover variant="dark">
                      <thead>
                          <tr>
                          <th>Photo</th>
                          <th><button onClick={this.handleSort} name="age">Age</button></th>
                          <th><button onClick={this.handleSort} name="first">First Name</button></th>
                          <th><button onClick={this.handleSort} name="last">Last Name</button></th>
                          <th><button onClick={this.handleSort} name="mobile">Mobile</button></th>
                          <th><button onClick={this.handleSort} name="country">Country</button></th>
                          </tr>
                      </thead>
                      <tbody>
                         {this.state.result.map((item,id) => (
                            <Worker key={id} workerData={item}></Worker>
                          ))}
                      </tbody>    
                  </Table>
            </Col> 
          </Row>   
        </Container>
    )
  }
}

export default Dictionary;
